import React from "react";
import ChildC from "./ChildC";

class ParC extends React.Component{
    constructor(){
        super();
        this.state={
            email:''
        }
    }
    change(e){
        this.setState({
            email:e.target.value
        })
    }
    render(){
        return(
            <div className="ParC">
                <span>{this.state.email}</span><br/>
                <ChildC name="email" change={this.change.bind(this)}/>
            </div>
        )
    }
}
export  default ParC;


